export default class Form { /* (создаем и сразу экспортируем класс, подключаем в main.js(чтобы запрос работал, запускать нужно на виртуальном сервере)) */
    constructor(forms) { /* (в конструктор будет приходить как аргумент только формы) */
        /* (перед началом работы рекомендуется проверить верстку - чтобы форма была стандартной, имела интерактивными полями не обычные divы, а инпуты, селекты и т.п, которые обязательно имеют аттрибут name, имела кнопку отправки(если запросы будут разными, рекомендуется вынести их в отдельный функционал)) */
        this.forms = document.querySelectorAll(forms);
        this.inputs = document.querySelectorAll('input'); /* (из формы получаем все инпуты - нужны будут для очистки после отправки) */
        this.message = { /* (создаем п с возможными сообщениями о статусе загрузки) */
            loading: 'Loading...',
            success: 'Thanks! We will contact you shortly!',
            failure: 'An error has occurred!'
        };
        this.path = 'assets/question.php'; /* (создаем п с адресом для запросов) */
    }

    clearInputs() { /* (создаем метод для очистки формы, подключаем после отправки формы) */
        this.inputs.forEach(item => {
            item.value = '';
        });
    }

    checkMailInputs() { /* (создаем метод для валидации инпутов ввода электронной почты на отсутствие кириллицы и вызываем в init()) */
        const mailInputs = document.querySelectorAll('[type="email"]'); /* (получаем из верстки инпуты по типу) */
        mailInputs.forEach(input => { 
            input.addEventListener('keypress', function(e) { /* (перебираем инпуты, навешиваем событие - нажатие кнопки) */
                if (e.key.match(/[^a-z 0-9 @ \.]/ig)) { /* (задаем условие - символы должны быть только латиница, цифры, собачка или точка) */
                    e.preventDefault();
                }
            });
        });    
    }

    initMask() { /* (создаем ф для установки маски номера телефона(взята из JS-4_15 - подробное описание там), вызываем в init()) */

        let setCursorPosition = (pos, elem) => {
            elem.focus();
            
            if (elem.setSelectionRange) {
                elem.setSelectionRange(pos, pos);
            } else if (elem.createTextRange) {
                let range = elem.createTextRange();

                range.collapse(true);
                range.moveEnd('character', pos);
                range.moveStart('character', pos);
                range.select();
            }
        };

        function createMask(event) {
            let matrix = '+1 (___) ___-____',
                i = 0,
                def = matrix.replace(/\D/g, ''),
                val = this.value.replace(/\D/g, '');

            if (def.length >= val.length) {
                val = def;
            }

            this.value = matrix.replace(/./g, function(a) {
                return /[_\d]/.test(a) && i < val.length ? val.charAt(i++) : i >= val.length ? '' : a;
            });

            if (event.type === 'blur') {
                if (this.value.length == 2) {
                    this.value = '';
                }
            } else {
                setCursorPosition(this.value.length, this);
            }
        }

        let inputs = document.querySelectorAll('[name="phone"]');

        inputs.forEach(input => {
            input.addEventListener('input', createMask);
            input.addEventListener('focus', createMask);
            input.addEventListener('blur', createMask);
        });
    }

    async postData(url, data) { /* (создаем метод для отправки формы( взят из предыдущего сайта, но в классах нужно использовать обычные ф, а не стрелочные)) */
        let res = await fetch(url, {
            method: "POST",
            body: data
        });

        return await res.text();
    }

    init() {
        this.checkMailInputs();
        this.initMask();
        this.forms.forEach(item => { /* (перебираем формы) */
            item.addEventListener('submit', (e) => { /* (назначаем событие отправки) */
                e.preventDefault(); /* (отменяем стандартное поведение браузера) */

                let statusMessage = document.createElement('div'); /* (для вывода инф о статусе отправки создаем блок) */
                statusMessage.style.cssText = `
                    margin-top: 15px;
                    font-size: 18px;
                    color: grey;
                `; /* (добавляем блоку стили) */
                item.parentNode.appendChild(statusMessage); /* (помещаем этот блок в родительский блок формы - будет добавлен сразу после формы, но нужно учитывать особенности верстки - может не поместиться) */

                statusMessage.textContent = this.message.loading; /* (после нажатия на кнопку отобразится сообщение о загрузке) */

                const formData = new FormData(item); /* (формируем данные для отправки - создаем новую FormDate с данными из формы(item)) */

                this.postData(this.path, formData) /* (запускаем метод по отправке, как арг передаем путь и данные для отправки, обрабатываем его через then/catch(не забываем, что это все идет одной строкой)) */
                    .then(res => { /* (в случае успеха выведем инф из формы в консоль и сообщение об успехе на страницу) */
                        console.log(res);
                        statusMessage.textContent = this.message.success;
                    })
                    .catch(() => { /* (в случае ошибки выводим соответствующее сообщение) */
                        statusMessage.textContent = this.message.failure;
                    })
                    .finally(() => { /* (в любом случае после отправки очищаем форму и удаляем сообщение о статусе через 6сек) */
                        this.clearInputs();
                        setTimeout(() => {
                            statusMessage.remove();
                        }, 6000)
                    });
            });
        });
    }
};  