export default class Slider { 
    constructor({
            container = null, 
            btns = null, 
            next = null, 
            prev = null,
            prevArr = null,
            nextArr = null,
            activeClass = '',
            animate = false,
            autoplay = false} = {}) {  
        this.container = document.querySelector(container);  
        try {this.slides = this.container.children;} catch(e) {}; /* (помешаем получение дочерних элементов в конструкцию try/catch - вторая страница сайта будет пытаться работать с этим скриптом, но вместо всех аргументов получит null - это ошибки не выдаст, но так как slides пытается получить дочерние элементы из null(блока container, кот не пришел), это вызовет ошибку и нарушит работу сайта) */
        this.btns = document.querySelectorAll(btns); 
        this.prev = document.querySelector(prev); 
        this.next = document.querySelector(next);
        this.prevArr = document.querySelectorAll(prevArr); 
        this.nextArr = document.querySelectorAll(nextArr);
        this.activeClass = activeClass;
        this.autoplay = autoplay;
        this.animate = animate;
        this.slideIndex = 1; 
    }
};


