import Slider  from "./slider"; 

export default class MainSlider extends Slider { 
    constructor (btns, nextArr, prevArr) {  
        super(btns, nextArr, prevArr); 
    }

    showSlides(n) { 
        if (n > this.slides.length) { 
            this.slideIndex = 1;
        }

        if (n < 1) { 
            this.slideIndex = this.slides.length;
        }


        try { 
            this.hanson.style.opacity = '0'; 

            if (n === 3) { 
                this.hanson.classList.add('animated');
                setTimeout(() => {
                    this.hanson.style.opacity = '1';
                    this.hanson.classList.add('slideInUp');
                }, 3000);
            } else {
                this.hanson.classList.remove('slideInUp'); 
            }
        } catch(e) {}
    
        for (let i = 0; i < this.slides.length; i++) {
            this.slides[i].style.display = "none"; 
        } 

        this.slides[this.slideIndex - 1].style.display = "block";
    }

    plusSlides(n) { 
        this.showSlides(this.slideIndex += n);
    }

    bindTriggers() { /* (выносим функционал нажатия кнопок из render в bindTriggers и подключаем в render) */

        this.btns.forEach(item => { 
            item.addEventListener("click", () => {
                this.plusSlides(1);
            });

            if (item.parentNode.previousElementSibling.tagName == 'A') {
                item.parentNode.previousElementSibling.addEventListener('click', (e) => {
                    e.preventDefault();
                    this.slideIndex = 1;
                    this.showSlides(this.slideIndex);
                });
            }
            
        });

          /* (навешиваем обработчики событий на кнопки переключения внутри слайдера) */
        /* document.querySelectorAll(this.prev) */this.prevArr.forEach(item => {
            item.addEventListener('click', (e) => {
                e.stopPropagation();
                e.preventDefault();
                this.plusSlides(-1);
            });
        });

        /* document.querySelectorAll(this.next) */this.nextArr.forEach(item => {
            item.addEventListener('click', (e) => {
                e.stopPropagation(); /* (в данном случае необходимо остановить всплытие(метод stopPropagation()) - при нажатии на кнопку перемотки она срабатывает дважды, так как назначена и на кнопку, и на ее родительский блок) */
                e.preventDefault();
                this.plusSlides(1);
            });
        });
    }

    render() {  
        if (this.container) { /* (вместо конструкции try/catch вводим условие наличия контейнера - если он есть - выполняем(это решает возникающие ранее проблемы)) */
                try { 
                    this.hanson = document.querySelector('.hanson'); 
                } catch(e) {}
            
            this.showSlides(this.slideIndex); 
            this.bindTriggers();
        
        }
    } 
}